.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 40px;
    z-index: 1;

    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav__contents {
  display: flex;
  justify-content: space-between;
}
.nav__logo {
  position: fixed;
  top: 10px;
  left: 0;
  width: 100px;
  object-fit: contain;
  padding-left: 20px;
  cursor: pointer;
}

.nav__avatar{
    position: fixed;
    right: 20px;
    width: 40px;
    /* height: 30px; */
    cursor: pointer;
}
.nav__black{
    background-color: black;
}